<template>
    <div class="w-full bg-lightblue border-b-2 border-dark mt-20 mb-10 p-2 input_field">
      <p class="text-dark text-center">Username</p>
      <input
          v-model="username"
          class="w-full bg-lightblue text-lg text-center"
          type="text"
          ref="username"
      />
    </div>
    <div class="w-full bg-lightblue border-b-2 border-dark mt-10s mb-10 p-2 input_field">
      <p class="text-dark text-center">Password</p>
      <input
          v-model="password"
          class="w-full bg-lightblue text-lg text-center"
          type="password"
      />
    </div>
    <div class="flex justify-center">
      <button
        v-if="!isLoading"
        @click.prevent="signIn"
        class="w-full md:w-1/3 font-bold rounded-full bg-blue text-white p-3 hover:bg-darkblue"
      >
        Log in
      </button>
      <div v-else class="w-full rounded-full mt-4 py-3">
        <ProgressBar/>
      </div>
    </div>
  </template>
  
<script>
import { mapActions } from "vuex";
import {defineAsyncComponent} from "vue";

export default {
  name: "LogInComponent",
  components: {
    ProgressBar: defineAsyncComponent(() => import('./ProgressBar.vue'))
  },
  data() {
    return {
      username: "",
      password: "",
      isLoading: false,
    };
  },
  created: function () {
    this.$nextTick(() => this.$refs.username.focus());
  },
  methods: {
    ...mapActions("authentication", ["signInUser", "logoutUser"]),
    async signIn() {
      this.isLoading = true;
      try {
        await this.signInUser({username: this.username, password: this.password});
      } catch (error) {
        await this.logoutUser();
        alert("Error signing in, please check console for error detail");
        console.log("error signing in", error);
      } finally {
        this.isLoading = false;
      }
    },
  },
};
</script>

<style scoped>
  .input_field {
    border-radius: 20px;
  }
  input:-webkit-autofill {
    background-color: #f4d5e1 !important;
    -webkit-box-shadow: 0 0 0px 1000px #f4d5e1 inset !important;
    border-radius: 8px;
  }
</style>
  